@import url('https://fonts.googleapis.com/css2?family=Lobster+Two:ital,wght@0,400;0,700;1,400;1,700&display=swap');

.booking-diagnostics-details-component {
    width: 100%;
    background-color: lightyellow;
}
/*----------------------------------    booking-diagnostic-detail-component-header    --------------------------------*/
.booking-diagnostic-detail-component-header {
    width: 100%;
    height: 150px;
    display: flex;
    align-items: center;
    justify-content: space-around;
    gap: 170px;
    background-color: lightyellow;
    border-bottom: 2px solid lightskyblue;
}
.logo-booking-diagnostic-detail-component {
    width: 200px;
    height: 120px;
    margin-right: 50px;
}
.booking-diagnostic-detail-component-header h1 {
    font-family: "Lobster Two", sans-serif;
    font-size: 80px;
    font-weight: 700;
    font-style: italic;
    color: blue;
}
.booking-diagnostics-detail-component-button {
    width: 100px;
    height: 45px;
    border-radius: 10px;
    border: none;
    background-color: blue;
    color: white;
    cursor: pointer;
}
.booking-diagnostics-detail-component-button:hover {
    background-color: green;
}
/*--------------------------------    booking-diagnostics-details-component-profile    -------------------------------*/
.booking-diagnostics-details-component-profile {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin: 15px 0 15px 0;
    gap: 25px;
}
/*---------------------------------------------    booking-diagnostics-details-component-button   --------------------*/
.booking-diagnostics-details-component-button {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin-bottom: 15px;
}
.booking-diagnostics-details-component-button-delete-booking {
    width: 170px;
    height: 45px;
    border-radius: 10px;
    border: none;
    background-color: red;
    color: white;
    cursor: pointer;
}