@import url('https://fonts.googleapis.com/css2?family=Lobster+Two:ital,wght@0,400;0,700;1,400;1,700&display=swap');

.recovery-password-container {
    width: 100%;
    background-color: lightyellow;
}
/*--------------------------------    recovery-password-container-header   -------------------------------------------*/
.recovery-password-container-header {
    width: 100%;
    height: 150px;
    display: flex;
    align-items: center;
    justify-content: space-around;
    gap: 170px;
    background-color: lightyellow;
    border-bottom: 2px solid lightskyblue;
}
.logo-recovery-password {
    width: 200px;
    height: 120px;
    margin-right: 50px;
}
.recovery-password-container-header h1 {
    font-family: "Lobster Two", sans-serif;
    font-size: 80px;
    font-weight: 700;
    font-style: italic;
    color: blue;
}
/*-------------------------------------------    recovery-password-form-div   ----------------------------------------*/
.recovery-password-form-div {
    width: 100%;
    height: 450px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 10px 0 10px 0;
}
.recovery-password-form {
    width: 450px;
    height: 450px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 40px;
    border-radius: 10px;
    background-color: gray;
}
.recovery-password-error {
    color: red;
}
/*------------------------------------------    recovery-password-form input   ---------------------------------------*/
.recovery-password-form input {
    width: 350px;
    height: 30px;
    padding: 10px 12px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 6px;
    outline: none;
    transition: border-color 0.2s ease;
}
.recovery-password-form input:focus {
    border-color: #007bff;
}
.recovery-password-form input::placeholder {
    color: #999;
    font-style: italic;
    opacity: 1;
}
.new-password-recovery {
    width: 450px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding-left: 25px;
}
.new-password-recovery-confirm {
    width: 450px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding-left: 25px;
}
.show-new-password-recovery-confirm {
    background: none;
    border: none;
    cursor: pointer;
    font-size: 18px
}
.show-new-password-recovery {
    background: none;
    border: none;
    cursor: pointer;
    font-size: 18px
}
/*----------------------------------------     recovery-password-button    -------------------------------------------*/
.recovery-password-button {
    width: 180px;
    height: 45px;
    border-radius: 10px;
    border: none;
    background-color: blue;
    color: white;
    cursor: pointer;
}
.recovery-password-button:hover {
    background-color: green;
}